import * as likelySubtags from './data/supplemental/likelySubtags.json';
import * as timeData from './data/supplemental/timeData.json';
import * as weekData from './data/supplemental/weekData.json';
import * as currencyData from './data/supplemental/currencyData.json';
import * as numberingSystems from './data/supplemental/numberingSystems.json';

import * as datesCAGeneric from './data/dates/de/ca-generic.json';
import * as datesCAGregorian from './data/dates/de/ca-gregorian.json';
import * as dateFields from './data/dates/de/dateFields.json';
import * as timeZoneNames from './data/dates/de/timeZoneNames.json';
import * as numbers from './data/numbers/de/numbers.json';
import * as currencies from './data/numbers/de/currencies.json';

const items: any[] = [
    likelySubtags,
    timeData,
    weekData,
    currencyData,
    numberingSystems,
    datesCAGeneric,
    datesCAGregorian,
    dateFields,
    timeZoneNames,
    numbers,
    currencies
];

(function () {
    try {
        for (let index = 0; index < items.length; ++index) {
            Globalize.load(items[index]);
        }
        Globalize.locale('de');
    }
    catch (e) {
    }
})();


